// import logo from './logo.svg';
import React from 'react';
import './App.css';
import { Route, Link, Redirect } from 'react-router-dom';
import pokemonData from './pokemonData';

class App extends React.Component {

  state = {
    pkmnTypes: [],
    pkmnList: pokemonData,
  }

  componentDidMount() {
    let types = [];

    for(let i = 0; i < pokemonData.length; ++i){
      if(!types.includes(pokemonData[i].type1)){
        types.push(pokemonData[i].type1);
      }
      if(pokemonData[i].type2 !== 'NA' && !types.includes(pokemonData[i].type2)){
        types.push(pokemonData[i].type2);
      }
    }

    this.setState({
      pkmnTypes: types, 
    });
  }

  render(){
    return(
      <main className="backgroundColor">
        <nav>
        <Link to="/All" className="navLinks">All</Link>
        {
          this.state.pkmnTypes.map(function(pkmnType){
            return (
              <Link to={'/' + pkmnType} className="navLinks" key={pkmnType}>
                {pkmnType}
              </Link>
            );
          })
        }
        {/* use anchor tag (a) to style Link */}
        </nav>
        <div className="pokemonContainer">
        
          <Route path="/">
            <Redirect to="/All" />
          </Route>

          <Route path="/All">
            {
              this.state.pkmnList.map(function(pokemon){
                return(
                  <div className="pokemonCard" key={pokemon.dexNo}>
                    <div>
                      <img 
                        src={pokemon.img} 
                        alt={pokemon.name} 
                        className="pokemonImg"
                      />
                    </div>
                    <div>
                      <p>#{pokemon.dexNo}</p>
                      <h2>{pokemon.name}</h2>
                      <span className="pokemonType">{pokemon.type1}</span>
                      {
                        pokemon.type2 !== 'NA' ? 
                        <span className="pokemonType">{pokemon.type2}</span> : null
                      }
                    </div>
                  </div>
                );
              })
            }
          </Route>
          {
            this.state.pkmnTypes.map(
              (type) => {
                return(
                  <Route path={'/' + type} key={type}>
                    {
                      this.state.pkmnList.filter(function(pokemon){
                        return (
                          pokemon.type1 === type || pokemon.type2 === type
                        );
                      }).map(function(pokemon){
                        return(
                          <div className="pokemonCard" key={pokemon.dexNo}>
                            <div>
                              <img 
                                src={pokemon.img} 
                                alt={pokemon.name} 
                                className="pokemonImg"
                              />
                            </div>
                            <div>
                              <p>#{pokemon.dexNo}</p>
                              <h2>{pokemon.name}</h2>
                              <span className="pokemonType">{pokemon.type1}</span>
                              {
                                pokemon.type2 !== 'NA' ? 
                                <span className="pokemonType">{pokemon.type2}</span> : null
                              }
                            </div>
                          </div>
                        );
                      })
                    }                  
                  </Route>
                );
              }
            )  
          }
          {/* <div className="pokemonCard">
            <div>
              <img src={pokemon.img} alt={pokemon.name} className="pokemonImg"/>
            </div>
            <div>
              <p>#{pokemon.dexNo}</p>
              <h2>{pokemon.name}</h2>
              <span>{pokemon.type1}</span>
              {pokemon.type2 !== 'NA' ? <span>{pokemon.type2}</span> : null}
            </div>
          </div> */}
        </div>
        {/* <Route path="/all">

        </Route>

        <Route path="/about">

        </Route>

        <Route path="/gallery">

        </Route> */}
      </main>
    );
  }

}
  
